#nav-main {
  border-radius: 4px;
  width: 80%;
  height: auto;
  margin-left: 10%;
  border: 8px solid #fff;
  background: lightblue;
}

#n-m-img {
  width: 100%;
  height: 400px;
  margin-bottom: 15px;
}

.n-m-nav {
  height: auto;
  background: #4700b3 !important;
  color: #fff !important;
  text-align: center;
}

body {
  background: #e67300;
  background: -webkit-linear-gradient(#e67300, #fff);
  background: -o-linear-gradient(#e67300, #fff);
  background: -moz-linear-gradient(#e67300, #fff);
  background: linear-gradient(#e67300, #fff);
}

.ad-mg-btm-0 {
  margin-bottom: 0 !important;
}

.n-m-nav li a {
  color: white !important;
}

.n-m-b-heading {
  width: 100%;
  height: 50px;
  background: #4700b3;
  padding: 10px;
  color: #fff;
}

.ad-bd {
  border: 3px solid #777;
  padding: 0 !important;
  border-radius: 4px;
}

.ad-pd {
  padding: 5px !important;
}

ul.templatemo-submenu {
  padding-left: 15px;
}

ul.templatemo-submenu li {
  list-style: none;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

ul.templatemo-submenu li:hover {
  background: #ccc;
  padding-left: 20px;
  cursor: pointer;
}

.nav-width {
  width: 80%;
  margin-left: 10%;
  height: auto;
}

.img-flex {
  width: 500px;
  height: 400px;
}

@media screen and (min-width: 992px) {
  .img-flex {
    width: 500px;
    height: 400px;
  }
}

@media screen and (max-width: 991px) {
  .at-media-1 {
    margin-top: 10px;
  }
  .ad-pd .col-md-3 img {
    height: 600px;
  }
  .ad-bd-1 {
    border-right: 3px solid #777 !important;
  }
}

@media screen and (max-width: 600px) {
  .ad-icon {
    display: none;
  }
}

html {
  margin-bottom: 50px !important;
}

.ad-flt-rt {
  float: right;
}

.ad-pd .col-md-3 img {
  width: 100%;
}

.n-m-b-body {
  background-color: #fff !important;
}

.ad-bd-1 {
  border: 3px solid #777;
  border-right: 0;
  border-radius: 4px;
  padding: 0 !important;
}

.ad-mg-tp {
  margin-top: 15px;
}

span.text-right.ad-a-w a {
  color: white !important;
}

div.navbar.navbar-inverse.n-m-nav.ad-mg-btm-0 a {
  text-decoration: none !important;
  color: white !important;
}

.box-shadow {
  box-shadow: 0 0 20px 3px #ddd;
}

.ad-icon {
  position: fixed;
  left: 0;
  top: 45%;
}

.fa-1 {
  padding-top: 15% !important;
  text-align: center;
  font-size: 25px !important;
  color: white;
  width: 60px;
  height: 60px;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  opacity: 0.7;
  padding-top: 15px !important;
}

.fa-1:hover {
  width: 100px;
  opacity: 1;
}

.fa-facebook {
  background: #3b5998;
}

.fa-linkedin {
  background: #007bb6;
}

.fa-google-plus {
  background: #dd4b39;
}

.ad-icon li {
  list-style: none;
  padding: 0;
}

.ad-nav-1 {
  text-align: left !important;
}

.ad-nav-2 {
  padding: 30px;
}

body {
  min-height: 100vh;
  padding-bottom: 10vh;
}

.img-index {
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 4px;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.img-index:hover {
  cursor: pointer;
  box-shadow: 0 0 20px 3px #ccc;
}

.ad-mg-tp {
  margin-top: 30px;
}

.ad-mg-tp-1 {
  margin-top: 15px;
}

.ad-pd-0 {
  padding: 0 !important;
}

.ad-6 {
  display: inline;
  padding: 15px;
  width: 50%;
}

.ad-bd-bd {
  border-radius: 4px !important;
}

.ad-bd-40 {
  border-radius: 40px !important;
}

.a-b {
  color: #333;
}

.a-b:hover, .a-b:focus {
  text-decoration: none;
  color: #333;
}

.a-b:hover b {
  text-shadow: 2px 2px 2px #000;
}

.red {
  color: red;
}

.b {
  text-shadow: 2px 2px 2px #000;
  font-size: 20px;
}

.ad-pd {
  padding: 15px;
}

.headding {
  border-bottom: 1px solid #ccc;
  box-shadow: 0 0 20px 1px #fff;
}

.iframe {
  border: 3px solid #fff;
  box-shadow: 0 0 20px 1px #fff;
  margin-bottom: 50px;
}

textarea {
  min-width: 100%;
  max-width: 100%;
  min-height: 200px;
  max-height: 300px;
}

.body {
  background-image: url("../images/contactus.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

.back-w {
  background: #f5f8fa;
  padding: 15px;
}

ul.nav.navbar-nav.navbar-right li a {
  color: #fff !important;
}

ul.nav.navbar-nav.navbar-right li a:hover {
  text-decoration: underline;
}
